import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/Login.vue'
import Users from '@/views/Users.vue'
import Page from '@/views/Page.vue'
import wei from '@/views/wei.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: '/',
    component: wei
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/users',
    name: 'users',
    component: Users
  },
  {
    path: '/page',
    name: 'page',
    component: Page
  }
]

const router = new VueRouter({
  base: 'adminadmin', // 如果项目项目在 域名 根据目录下，则去掉这行
  routes
  // const router = new VueRouter({
  //   mode: 'history',
  //   routes
  // })
})

export default router
