<template>
  <div class="users">
    <el-table :data="tableData" :row-class-name="tableRowClassName" style="width: 100%">
      <el-table-column prop="_id" label="id" width="50">
      </el-table-column>
      <el-table-column prop="userName" label="userName" width="100">
      </el-table-column>
      <el-table-column prop="email" label="email" width="150">
      </el-table-column>
      <el-table-column prop="permission" label="permission" width="100">
        <template slot-scope="scope">
          <el-tag :type="tablePermission(scope.row.permission)" disable-transitions>
            {{scope.row.permission}}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="status" width="63">
      </el-table-column>
      <el-table-column label="操作" width="160">
        <template slot-scope="scope">
          <el-button @click.native.prevent="openDrawer('ban_drawer', scope)" type="text" size="small">
            封禁
          </el-button>
          <el-button @click.native.prevent="openDrawer('editPass_drawer', scope)" type="text" size="small">
            改密码
          </el-button>
          <el-button @click.native.prevent="openDrawer('permission_drawer', scope)" type="text" size="small">
            改权限
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-drawer title="封禁" :visible.sync="ban_drawer" :with-header="true">
      <h3>id: {{ edited_id }}</h3>
      <h3>name: {{ edited_name }}</h3>
      <el-button type="warning" @click="updata('status', 2)">封禁</el-button>
      <el-button type="success" @click="updata('status', 1)">解禁</el-button>
    </el-drawer>

    <el-drawer title="修改密码" :visible.sync="editPass_drawer">
      <h3>id: {{ edited_id }}</h3>
      <h3>name: {{ edited_name }}</h3>
      <el-input v-model="editPass_new" placeholder="请输入内容"></el-input>
      <el-button @click="updata('passWord', editPass_new)">修改</el-button>
    </el-drawer>

    <el-drawer title="修改权限" :visible.sync="permission_drawer">
      <h3>id: {{ edited_id }}</h3>
      <h3>name: {{ edited_name }}</h3>
      <el-button type="success" @click="updata('permission', 'member')">成员</el-button>
      <el-button type="primary" @click="updata('permission', 'operator')">管理员</el-button>
      <el-button type="warning" @click="updata('permission', 'admin')">admin</el-button>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      ban_drawer: false,
      editPass_drawer: false,
      permission_drawer: false,
      edited_id: '',
      edited_name: '',

      editPass_new: ''
    }
  },
  created() {
    this.initUser()
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      // console.log(rowIndex)
      // console.log(row)
      // console.log(row.status)
      if (row.status === 1) {
        return 'success-row'
      } else {
        // return 'success-row'
        return 'warning-row'
      }
      // return 'aaa'
    },
    tablePermission(permission) {
      if (permission === 'admin') {
        return 'danger'
      } else if (permission === 'member') {
        return 'success'
      } else if (permission === 'warning') {
        return 'warning'
      }
      // console.log(row, column)
    },
    async initUser() {
      const { data: res } = await this.$http.post('/api/admin/user/getusers')
      this.tableData = res
    },
    openDrawer(name, e) {
      this[name] = true
      this.edited_id = e.row._id
      this.edited_name = e.row.userName
    },
    updata(type, value) {
      this.$confirm('确定要退修改吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.$http
            .post('/api/admin/user/edit', {
              type,
              value,
              _id: this.edited_id
            })
            .then(res => {
              console.log(res)
              this.$alert(res.data)

              this.$confirm('更新数据?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              })
                .then(() => {
                  this.initUser()
                })
                .catch(() => {
                  this.$message({
                    type: 'info',
                    message: '已取消'
                  })
                })
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .el-drawer {
  min-width: 300px;
  padding-left: 15px;
}

/deep/ .el-table .warning-row {
  background: oldlace;
}

/deep/ .el-table .success-row {
  background: #f0f9eb;
}
</style>
