<template>
  <div class="login">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="密钥1" prop="pass">
        <el-input type="password" v-model="ruleForm.pass1" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密钥2" prop="pass">
        <el-input type="password" v-model="ruleForm.pass2" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密钥3" prop="pass">
        <el-input type="password" v-model="ruleForm.pass3" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('numberValidateForm')">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import axios from 'axios'
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    var validatePass3 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        pass1: '',
        pass2: '',
        pass3: ''
      },
      rules: {
        pass1: [{ validator: validatePass, trigger: 'blur' }],
        pass2: [{ validator: validatePass2, trigger: 'blur' }],
        pass3: [{ validator: validatePass3, trigger: 'blur' }]
      }
    }
  },
  methods: {
    async submitForm(formName) {
      // axios.get(`/api/admin/login?passWord1=${this.ruleForm.pass1}&passWord2=${this.ruleForm.pass2}&passWord3=${this.ruleForm.pass3}`)
      console.log({
        passWord1: this.ruleForm.pass1,
        passWord2: this.ruleForm.pass2,
        passWord3: this.ruleForm.pass3
      })
      const { data: res } = await this.$http({
        url: '/api/admin/login',
        method: 'post',
        data: {
          passWord1: this.ruleForm.pass1,
          passWord2: this.ruleForm.pass2,
          passWord3: this.ruleForm.pass3
        }
      })
      if (res.code === 200) {
        this.$router.push('/')
      }
      // this.$refs[formName].validate(valid => {
      //   if (valid) {
      //     alert('submit!')
      //   } else {
      //     console.log('error submit!!')
      //     return false
      //   }
      // })
    }
  }
}
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  height: 100vh;
  z-index: 99999999999999999999;
  background-color: #fff;
  position: fixed;
}
</style>
