<template>
  <div class="users">
    <el-table :data="tableData" style="width: 100%" height="90vh">
      <el-table-column width="50" type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="id">
              <span>{{ props.row._id }}</span>
            </el-form-item>
            <el-form-item label="title">
              <span>{{ props.row.title }}</span>
            </el-form-item>
            <el-form-item label="info">
              <span>{{ props.row.info }}</span>
            </el-form-item>
            <el-form-item label="date">
              <span>{{ props.row.date }}</span>
            </el-form-item>
            <el-form-item label="status">
              <span>{{ props.row.status }}</span>
            </el-form-item>
            <el-form-item label="dataUuid">
              <span>{{ props.row.dataUuid }}</span>
            </el-form-item>
            <el-form-item label="author">
              <div>id: {{ props.row.author._id }}</div>
              <div>userName: {{ props.row.author.userName }}</div>
              <div>email: {{ props.row.author.email }}</div>
              <div>permission: {{ props.row.author.permission}}</div>
              <div>status: {{ props.row.author.status }}</div>
            </el-form-item>
            <el-form-item label="edited">
              <div v-for="i in props.row.edits" :key="i.length">
                id: {{ i.user }}
                Date: {{i.date}}
              </div>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="_id" label="id" width="40">
      </el-table-column>
      <el-table-column prop="title" label="title" width="100">
      </el-table-column>
      <el-table-column prop="info" label="info" width="150">
      </el-table-column>
      <el-table-column prop="author.userName" label="author" width="100">
      </el-table-column>
      <el-table-column prop="status" label="status" width="63">
      </el-table-column>
      <el-table-column prop="dataUuid" label="dataUuid" width="180">
      </el-table-column>
      <el-table-column label="操作" width="160">
        <template slot-scope="scope">
          <el-button @click.native.prevent="openDrawer('edited_drawer', scope)" type="text" size="small">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-drawer title="删除" :visible.sync="edited_drawer" :with-header="true">
      <h3>id: {{ edited_id }}</h3>
      <h3>title: {{ edited_title }}</h3>
      <el-button type="warning" @click="updata('status', 3)">删除</el-button>
      <el-button type="success" @click="updata('status', 1)">恢复</el-button>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      edited_drawer: false,
      edited_id: '',
      edited_title: '',

      editPass_new: ''
    }
  },
  created() {
    this.initPage()
  },
  methods: {
    async initPage() {
      const { data: res } = await this.$http.post('/api/admin/page/getPages')
      this.tableData = res
    },
    openDrawer(name, e) {
      this[name] = true
      this.edited_id = e.row._id
      this.edited_title = e.row.title
    },
    // banUser(e) {
    //   this.edit_id = e.row._id
    //   this.ban_drawer = true
    //   // console.log(_id)
    // },
    // editPass(e) {
    //   this.editPass_new = ''
    //   this.edit_id = e.row._id
    //   this.editPass_drawer = true
    // },
    // editPermission(e) {
    //   this.edit_id = e.row._id
    //   this.permission_drawer = true
    // }
    updata(type, value) {
      this.$confirm('确定要退修改吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.$http
            .post('/api/admin/page/edit', {
              type,
              value,
              _id: this.edited_id
            })
            .then(res => {
              console.log(res)
              this.$alert(res.data)

              this.$confirm('更新数据?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              })
                .then(() => {
                  this.initPage()
                })
                .catch(() => {
                  this.$message({
                    type: 'info',
                    message: '已取消'
                  })
                })
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .demo-table-expand {
  font-size: 0;
  label {
    width: 90px;
    color: #99a9bf;
  }
  .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
}
/deep/ .el-drawer {
  min-width: 300px;
  padding-left: 15px;
}
</style>
